.nav{
  display: flex;
  border-bottom: 1px solid #080708;
  height: 10vh;
  background-color: black;
  opacity: .8
}

.nav__container{
  color:#6B0504;
  display: flex;
  width: 200px;
  justify-content: center;
  align-items: center;
  margin: auto;
}

.nav__container span{
  margin-right: 10px;
  width: 40px;
  animation: goLeft .5s ease-in;
}

@keyframes goLeft {
  from{transform: translateX(-100px);}
  to{transform: translateX(0px);}
}
