@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');

*{
  margin: 0px;
  padding: 0px;
  font-family: 'Montserrat', sans-serif;
}

a{
  text-decoration: none;
  color: inherit;
}

ul{
  list-style: none;
}

.app {
  width: 100%;
}

.right{
    background-color: #0b071f;
}

.right h1 {
  color: whitesmoke;
  display: flex;
  width: max-content;
  margin: auto;
}

.top{
  margin: 0px;

  color: white;
  width: 100%;
  text-align: center;
  padding-top: 15px;
}

.app__body{
  display: flex;
  width: 100%;
}

.left{
  display: flex;
  flex-direction: column;
  width: 80%;
}

.left .genres__container{
  display: flex;
  flex-wrap: wrap;
  height: 1100px;
  overflow-y: scroll;
}

.left h1 {
  margin-top: 30px;
  margin-left: 50px;
}

.genres__selector {
  background-color: #614ccb;
  width: 30%;
  margin: auto;
  margin-top: 0px;
  margin-bottom: 30px;
  display: flex;
  justify-content: center;
  color: white;
  padding: 15px;
}

.genres__selector svg{
  color: #ffff00;
}

.right{
  width: 20%;
}

.schedule__days{
  display: flex;
  margin: auto;
  margin-top: 0px;
  margin-bottom: 30px;
  font-size: 20px;
  background-color: #684dca;
}

.schedule__days li{
  background-color: inherit;
  margin: 20px;
  padding: 15px;
  cursor: pointer;
  border-radius: 10px;
  transition: all .2s ease-in
}

.schedule__days li:first-child{
    background-color: #ffff00;
}
.reload{
  margin:auto;
}