.animeCard{
  width: 250px;
  height: 300px;
  color: white;
  background-color:#0A100D;
  padding: 0px;
  margin: auto;
  margin-top: 15px;
  position: relative;
  transition: transform .2s ease-in;
  cursor: pointer;
}


.animeCard:hover{
  transform: scale(1.02);
  transform-origin: center;
}

.animeCard img{
  width: 250px;
  height: 250px;
}

.animeCard p{
  font-size: 15px;
  padding: 5px 5px 5px 5px;
}

.card__rank {
  position: absolute;
  top: 0px;
  left: 0px;
  background-color: yellow;
  width: 30px;
  height: 30px;
  font-size: 25px;
  color: black
}



.genres .animeCard{
  height: 350px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: auto;
}

.genres .animeCard p{
  font-size: 12px;
}

.card__info{
  display: flex;
  justify-content: space-between;
  padding: 20px;
  color: yellow;
}

.trailer{
  width: 100%;
  height: 300px;
  display:none;
  position: relative;
  position: sticky;
  position: -webkit-sticky;
  top: 0px;
  z-index: 1
}

.trailer iframe{
  width: 100%;
  height: 100%;
  border: none;
}

.trailer button{
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  right: 0px;
  top: 0px;
  cursor: pointer;
  background: transparent;
  border: none;
  height: 30px;
  width: 30px;
}

.trailer button span{
  color: white;
  font-size: 30px;
  opacity: .5
}
